// app/shop/category/[category]/page.tsx

import ProductCard from "@/app/components/After_log_homepage/ProductCard";
import { mockProducts } from "@/lib/mockData";

export default function CategoryPage({
  params,
}: {
  params: { category: string };
}) {
  const cleanCategory = params.category.toLowerCase().replace(/-/g, " ");
  const products = mockProducts.filter(
    (p) => p.category.toLowerCase() === cleanCategory
  );

  return (
    <div className="p-6">
      <h1 className="text-3xl font-bold mb-8 capitalize">{cleanCategory}</h1>
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
        {products.map((product) => (
          <ProductCard key={product.id} product={product} />
        ))}
      </div>
    </div>
  );
}